.loading {
    color: white;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 30px;
    padding: 16px;
}

.items {
    display: flex;
    gap: 25px;
    margin-bottom: 30px;
    justify-content: center;
    flex-wrap: wrap;
}

.fav-wrapper {
    display: flex;
    flex-direction: column;
}

.fav-wrapper .fav-items {
    padding: 25px;
    display: flex;
    gap: 25px;
    margin-bottom: 30px;
    overflow: auto;
}

h1 {
    color: rgb(242, 255, 0);
    font-size: 50px;
    text-shadow: 2px 2px 12px white;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.no-recipe {
    color: orangered;
    text-shadow: 2px 2px 20px whitesmoke;
    font-size: 50px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.no-fav-recipe {
    color: orangered;
    text-shadow: 2px 2px 20px whitesmoke;
    margin-top: 30px;
    font-size: 50px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.header{
    color: rgb(255, 230, 0);
    font-size: 70px; 
    font-style: italic;  
    text-shadow: 2px 2px 5px wheat;
    font-stretch: ultra-expanded;

}