.fav-item {
    border: 1px solid red;
    border-radius: 10px;
    padding: 10px;
    box-shadow: 0px 0px 10px 0px rgba(250, 100, 0, 1);
    background-color: white;
    transition: transform 0.5s;
}

.fav-item:hover {
    transform: scale(1.1);
}

.fav-item button {
    background-color: sandybrown;
    border-color: sandybrown;
    font-size: 15px;
    box-shadow: 0px 0px 10px 0px rgba(250, 100, 0, 1);
    padding: 10px 50px;
    border-radius: 10px;
    cursor: pointer;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.fav-item p {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: bolder;
    font-size: 18px;
}