.Search {
    display: flex;
    gap: 20px;
    justify-content: center;
}

.Search input {
    padding: 10px 50px;
    border-radius: 5px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 18px;
    transition: transform 0.3s;
}

.Search input:hover {
    transform: scale(1.1);
}

.Search button {
    background-color: sandybrown;
    text-shadow: 2px 0px 2px white;
    font-size: 18px;
    padding: 10px 50px;
    border-radius: 5px;
    cursor: pointer;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    transition: transform 0.3s;
}

.Search button:hover {
    transform: scale(1.1);
}